* {
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
body {
    font-family: 'Fira Code', monospace;
    color: white;
    background-color: black;
    overflow-x: hidden;
    overflow-y: hidden;

}


#sideBarButton{
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
    -o-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
    position: absolute;
    right: var(--arrowButton-position, 3px);
    margin-top: 90px;
    height: 30px;
    width: 20px;
    background: rgb(36, 36, 36);
    border-radius: 3px;
    box-shadow: inset 0 0 0 1px rgb(54, 54, 54);
}
#arrow{
    position: absolute;
    top: -88px;
    left: var(--arrow-position, -92px);
    scale: 0.05;
    rotate: var(--arrow-direction, 130deg);
}
#arrow:hover{
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
}

#sideBar {
    display: inline-block;
    visibility: var(--sidebar-vis, hidden);
    position: absolute;
    opacity: var(--sidebar-opacity, 0);
    right: 0px;
    margin-top: 35px;
    width: 75px;
    height: 150px;
    background: rgb(36, 36, 36);
    scale: 0.8;
    border-radius: 10px;
    box-shadow: inset 0 0 0 2px rgb(54, 54, 54)
}

.anim1{
    animation-iteration-count: 1;
    animation-duration: 1s;
    animation-name: sidebarAnim;
}
a {
display: inline-block;
}

#button1 {
    display: inline-block;
    opacity: var(--sidebar-opacity, 0);
    visibility: var(--sidebar-vis, hidden);
    position: absolute;
    width: 50px;
    height: 50px;
    background: rgb(74, 74, 74);
    border-radius: 10px;
    right: 12px;
    margin-top: 40px;
    cursor: pointer;
}
.vkimg{
    width: 40px;
    height: 25px;
    margin-left: 5px;
    margin-top: 10px;
    margin-bottom: 20px;
}
#button1:hover{
    box-shadow: 
    1px 1px #000000,
    2px 2px #000000;
    -webkit-transform: translateX(-2px);
    transform: translateX(-2px);
}

#button2{
    display: inline-block;
    visibility: var(--sidebar-vis, hidden);
    opacity: var(--sidebar-opacity, 0);
    position: absolute;
    width: 50px;
    height: 50px;
    background: rgb(74, 74, 74);
    border-radius: 10px;
    right: 12px;
    margin-top: 135px;
    cursor: pointer;
}
.dsimg{
    width: 40px;
    height: 40px;
    margin-top: 5px;
    margin-left: 5px;
    opacity: 0.6;
}
#icon2{
    margin-top: -59px;
    margin-left: 60px;
}
#button2:hover{
    box-shadow: 
    1px 1px #000000,
    2px 2px #000000;
    -webkit-transform: translateX(-2px);
    transform: translateX(-2px);
}

@keyframes sidebarAnim{
    0%{
        opacity: 0;
    }
    10%{
        opacity: 0.1;
    }
    20%{
        opacity: 0.2;
    }
    30%{
        opacity: 0.3;
    }
    40%{
        opacity: 0.4;
    }
    50%{
        opacity: 0.5;
    }
    60%{
        opacity: 0.6;
    }
    70%{
        opacity: 0.7;
    }
    80%{
        opacity: 0.8;
    }
    90%{
        opacity: 0.9;
    }
    100%{
        opacity: 1;
    }
}

.square1{
    position: relative;
    top: 30vh;
    margin-left: auto;
    margin-right: auto;
    width: 300px ;
    height: 220px ;
    background: rgb(36, 36, 36);
    border-radius: 10px;
    box-shadow: inset 0 0 0 2px rgb(54, 54, 54);
    animation-iteration-count: 1;
    animation-duration: 5s;
    animation-name: squareAnim;
}
@keyframes squareAnim{
    0%{
        width: 5px;
        height: 0px;
    }
    50%{
        height: 220px;
        width: 5px;
    }
    100%{
        width: 300px;
        height: 220px;
    }
}

.HHeader{
    position: fixed;
    margin-left: -10px;
    height: 60px;
    width: 102%;
    background: rgb(36, 36, 36);
}

.DatePicker{
    position: absolute;
    top: 30px;
    left: 20px;
    opacity: 1;
    animation-name: FormAnim;
    animation-iteration-count: 1;
    animation-duration: 5.5s;
}
@keyframes FormAnim {
    0%{
        opacity: 0;
    }
    90%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

.TextField{
    position: absolute;
    left: 20px;
    top: 45px;
    opacity: 1;
    animation-name: FormAnim;
    animation-iteration-count: 1;
    animation-duration: 5.5s;
}
